
.city-names-screen {
    height: 100svh;
    width: 100svw;
    font-size: 1.5rem;
    background-color: hsl(0, 0%, 35%);
    color: hsl(0,0%, 90%);
    overflow-y: auto;
}

.city-names-title {
    height: 20svh;
    width: 100svw;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 3svh 0 5svh ;
    margin-bottom: 5svh;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    line-height: 1.5;
    border-bottom:  1px solid hsl(0, 0%, 100%);
    background-color: hsl(0, 0%, 30%);
}

.city-names-screen-element {
    display: flex;
    flex-direction: row;
    max-width: 95svw;
    margin: 1rem auto;
    flex-wrap: wrap;
    padding: 0 5svh;
    letter-spacing: 1.25px;
    gap: 1rem;
}



.city-names-screen-element-countryName {
    font-size: 1.25rem;
    padding: 1rem 0 0.5rem 0;
    color: hsl(0,0%, 100%);
    border-bottom: 1px solid hsl(0,0%, 100%);
}

@media screen and (max-width:700px){
    .city-names-title {
        justify-content: center;
        height: 15svh;
    }
    .city-names-screen-element-cityName {
        padding: .5svh 1rem;
    }
}
.city-names-screen-element-cityName {
    height: max-content;
    width: 100%;
    font-size: 1.15rem;
    display: flex;
    flex-wrap: wrap;
}

.city {
    width: 15ch;
    padding: 5svh 1.5svw;
    text-align: left;
}

